/* -- Main menu -- */
.open-mobile{
    .menu{
        &-main{
            flex-wrap: wrap;
            justify-content: flex-start;
            margin-top: 14px;
            background: $color-lightgray;
        }
        &-item{
            border-bottom: 1px solid $color-darkgray;
            width: 100%;
            text-align: left;
            &:last-child{
                border-bottom: none;
            }
        }
        &-link{
            padding: 6px 12px;
            display: block;
        }
    }
}

.menu{
    &-main{
        position: relative;
        z-index: 10;
        &--submenu{
            position: absolute;
            display: none;
            padding: 0;
            width: 216px;
            background: $color-white;
            box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.29);
            top: 28px;
            .menu{
                &-item{
                    margin: 0;
                    padding: 0;
                    display: block;
                    text-align: left;
                }
                &-link{
                    display: block;
                    font-size: 14px;
                    padding: 10px 16px;
                    color: $color-darkgray !important;
                    border: 0 !important;
                    &:hover{
                        background: $color-primary;
                        color: $color-white !important;
                    }
                }

            }
        }
    }
    &-item{
        &:hover{
            // 128
            .menu-link{
                color: $color-primary;
                padding-bottom: 6px;
                border-bottom: 1px solid $color-primary;
            }
        }
    }
}

//  Menu responsive - btn
.navbar-brand__btn-menu{
    display: block;
    position: absolute;
    right: 16px;
    padding: 0;
    width: 32px;
    border: 0;
    background: transparent;
    cursor: pointer;
    top: 16px;
    &:focus{
        outline:0;
    }
    span{
        height: 3px;
        display: block;
        background: $color-black;
        width: 100%;
        margin: 3px 0;
    }
    @media (min-width: $size-tablet2){
        display: none;
    }
}