.r-copy-2{
    ul{
        margin: 10px 0 0;
        text-align: center;
        @media (min-width: $size-tablet2){
            margin: 0;
            text-align: right;
        }
        li{
            display: block;
            @media (min-width: $size-tablet2){
                display: inline-block;
                vertical-align: top;
            }
            a{
                color: $color-white;
            }
        }
    }
}